import React from 'react';
import logo from './logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from './index_data.json';
import {HashRouter as Router,Route,Link} from 'react-router-dom';
import App from './App';
class Main extends React.Component {
  render()
  {
  return (
    <Router>
    <Route exact path="/" component={Main1}/>
    <Route exact path="/App" component={App}></Route>
    </Router>
  );
  }
}

function Main1()
{
 
  return(
  <div className="App">
  <h1>Please see the output</h1>
  <div className="row">
      <div className="card col-md-4 offset-4" style={{textAlign: "center"}}>
        <img src={logo}></img>
        <h1>{data.candidates[3].Full_Name}</h1>
        <h2>{data.candidates[3].Email}</h2>
        <Link to="/App" className="btn-primary">Click</Link>
      </div>
  </div>
</div>);
  
}

export default Main;